.MuiContainer-root {
  margin-left: 0 !important;
}

.paper {
  width: 30%;
  display: flex;
  margin: 2em auto;
}

.form {
  width: 90%;
  margin-left: 0;
}

.form Input {
  height: 10px;
}

.form Button {
  background-color: #4b54db;
  margin: 0 5px 5px 15px;
  width: 30%;
  height: 3em;
  display: inline-block;
}

.form Button:hover {
  background-color: #6971dd;
}
.submit {
  margin-top: 30px;
}

@media (max-width: 1050px) and (min-width: 760px) {
  .form Button {
    width: 40%;
  }
}

@media (max-width: 780px) {
  .paper {
    width: 80%;
  }
}
